          href: 'https://vuetifyjs.com/components/api-explorer'
        },
        {
          text: 'Select a layout',
          href: 'https://vuetifyjs.com/layout/pre-defined'
        },
        {
          text: 'Frequently Asked Questions',
          href: 'https://vuetifyjs.com/getting-started/frequently-asked-questions'
        }

      ]
    }),
    mounted() {

        axios.post("http://localhost:8081/api/v1/dashboard").then((res)=>{

        this.sensor_online = res.data.Online
        this.sensor_offline = res.data.Offline
        this.sensor_all = res.data.All
        this.avg_pm10 = res.data.PM10
        this.avg_pm25 = res.data.PM25
        this.avg_temp = res.data.Temp
        this.avg_humi = res.data.Humi
        })

    }
  }
</script>

